  methods: {
    afterRead(file) {
      console.log(file, this.fileList, 8);
      // if (this.fileList.length > 3) {
      //   this.$toast.fail("图片上传最多三张!");
      //   this.fileList.splice(3);
      //   return;
      // }
      // let formData = new FormData();
      // formData.append("file", file.file);

      // this.$api.shopOrdersReturnUpload(formData).then((res) => {
      //   if (!res.success) {
      //     this.$toast.fail("图片上传失败,请重新上传!");
      //     return;
      //   }
      //   if (res.data) {
      //     this.$toast.success("图片成功!");
      //     this.fileList[this.fileList.length - 1].r_path = res.data.r_path;
      //   }
      // });
      // 此时可以自行将文件上传至服务器
      if (file.length) {
        //判断是否选择了多个文件，如果是的话，循环遍历单个文件上传
        file.forEach((f) => {
          this.uploadFile(f);
        });
      } else {
        this.uploadFile(file);
      }
    },
    // 上传文件到服务器
    uploadFile(file) {
      const fd = new FormData();
      fd.append("file", file.file);
      fd.append("fileType", "file");
      const Toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "上传中",
      });
      Toast.clear();

      this.fileList.push({
        fileName: file.file.name,
        filePath: "",
        fileType: file.file.type || "file",
      });

      // govMobieUploadFiles(fd)
      //   .then((data) => {
      //     Toast.clear();

      //     if (data) {
      //       this.fileList.push({
      //         fileName: file.file.name,
      //         filePath: data,
      //         fileType: file.file.type || "file",
      //       });
      //       this.$message.success("上传成功！");
      //     } else {
      //       this.$message.error("上传失败");
      //     }
      //   })
      //   .catch(() => {
      //     Toast.clear();
      //   });
    },
