      this.ir = index;
    },
    goDetail() {
      this.$router.push({ name: "zhaoDetail" });
    },
    goAnother() {
      this.$router.push({ name: "newIndex" });
    },
    goSub(title, href) {
      console.log(title, href);
      // if (title == "双选会") {
      //   this.$router.push({ name: "shuangMore" });
      // } else if (title == "招聘信息") {
      //   this.$router.push({ name: "zhaoMore" });
      //   this.$store.commit("updataTypeText", 0);
      // } else if (title == "宣讲会") {
      //   this.$router.push({ name: "xuanMore" });
      // } else {
      // }

      // window.location.href = href;
      // this.$router.push({ name: "activityMore" });

      var kl = [
        {
          id: 1,
          title: "首页",
          img: require("../assets/one-hover.png"),
          img1: require("../assets/one.png"),
          routerName: { name: "Index" },
        },
        {
          id: 2,
          title: "新闻活动",
          img: require("../assets/two-hover.png"),
          img1: require("../assets/two.png"),
          routerName: { name: "newsMore" },
        },
        {
          id: 3,
          title: "精准推送",
          img: require("../assets/three-hover.png"),
          img1: require("../assets/three.png"),
          routerName: { name: "Push" },
        },
        {
          id: 4,
          title: "手续办理",
          img: require("../assets/four-hover.png"),
          img1: require("../assets/four.png"),
          routerName: { name: "Procedures" },
        },
