          id: 5,
          title: "我的",
          img: require("../assets/five-hover.png"),
          img1: require("../assets/five.png"),
          routerName: { name: "Mine" },
        },
      ];

      localStorage.setItem(
        "LIST4",
        JSON.stringify(JSON.parse(JSON.stringify(kl)))
      );
      this.$router.push({ name: "newsMore" });
      this.reload();
    },
    getList() {
      const params = {
        type: "mobile",
        num: 6,
      };
      console.log(params);
      this.$axios
        .get("/f/ajaxHome/ajax_quickEntryList?type=mobile" + "&num=" + 6)
        .then((res) => {
          console.log(res, "中部");
          if (res.data.state == 1) {
            if (res.data.data) {
              this.list = res.data.data;
            }
          } else {
            this.list = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getUser() {
      this.$axios
        .get("/f/ajaxHome/ajax_getUser")
        .then((res) => {
          console.log(res, "获取用户");
          if (res.data.state == 1) {
            this.userBody = res.data.object;
          } else {
            this.userBody = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    // 获取导航栏
    this.getList();
    // 获取用户
    this.getUser();
  },
  beforeDestory() {},
  destroyed() {},
};
